import React, { Component } from 'react';
import Button from '@material-ui/core/Button';
import ButtonAppBar from "./ButtonAppBar";

class App extends Component {
  render() {
    return (
      <div className="App">
          <ButtonAppBar />
          <Button variant="contained" color="primary">
              Hello World
          </Button>
      </div>
    );
  }
}

export default App;



